import React from 'react';

//<div className="index-img"><img src={this.state.img}/></div>
//<div className="index-line"><span className="times">{this.state.id} - </span><span className="helvetica" onMouseOver={() => this.testHover()}>{this.state.title}</span></div>
class HelpText extends React.Component {
    constructor(props){
      super(props);
      var msg = "You may think you're on your own now, but you can always come to me for guidance";
      msg = msg.split(" ");

      //console.log("TEST: " + JSON.stringify(props));
      this.state = {
        message: msg,
        offsets: this.getCharacterAnimationTimes(msg),

        potentialAffirmations: [
          "You got this",
          "You'll figure it out",
          "You're a smart cookie",
          "I believe in you",
          "Sucking at something is the first step towards being good at something",
          "Struggling is part of learning",
          "Everything has cracks - that's how the light gets in",
          "Mistakes don't make you less capable",
          "We are all works in progress",
          "You are a capable human",
          "You know more than you think",
          "10x engineers are a myth",
          "If everything was easy you'd be bored",
          "I admire you for taking this on",
          "You're resourceful and clever",
          "You'll find a way",
          "I know you'll sort it out",
          "Struggling means you're learning",
          "You're doing a great job",
          "It'll feel magical when it's working",
          "I'm rooting for you",
          "Your mind is full of brilliant ideas",
          "You make a difference in the world by simply existing in it",
          "You are learning valuable lessons from yourself every day",
          "You are worthy and deserving of respect",
          "You know more than you knew yesterday",
          "You're an inspiration",
          "Your life is already a miracle of chance waiting for you to shape its destiny",
          "Your life is about to be incredible",
          "Nothing is impossible. The word itself says 'Im possible!'",
          "Failure is just another way to learn how to do something right",
          "I give myself permission to do what is right for me",
          "You can do it",
          "It is not a sprint, it is a marathon. One step at a time",
          "Success is the progressive realization of a worthy goal",
          "People with goals succeed because they know where theyre going",
          "All you need is the plan, the roadmap, and the courage to press on to your destination",
          "The opposite of courage in our society is not cowardice... it is conformity",
          "Whenever were afraid, its because we dont know enough. If we understood enough, we would never be afraid",
          "The past does not equal the future",
          "The path to success is to take massive, determined action",
          "Its what you practice in private that you will be rewarded for in public",
          "Small progress is still progress",
          "Don't worry if you find flaws in your past creations, it's because you've evolved",
          "Starting is the most difficult step - but you can do it",
          "Don't forget to enjoy the journey",
          "It's not a mistake, it's a learning opportunity",
        ]
      };
      
      window.addEventListener('click', (event) => {
        //console.log("HELPTEXT: CLICKEDDD");
        this.getAffirmation();
      });
      //this.getAffirmation();
    }

    getAffirmation(){
      var newAffirmation = Math.floor(Math.random() * this.state.potentialAffirmations.length);
      var msg = this.state.potentialAffirmations[newAffirmation].split(" ");
      //console.log("affirmation: " + msg);
      this.setState ({
        message: msg
      });
    
    }

    getCharacterAnimationTimes(msg){
      var offsets = []
      for (var i = 0; i < msg.length; i++) 
      {
        offsets.push(Math.floor((Math.random() * 8) + 1));
      }
      return offsets;
    }

    getIndividualLetters(){
      var letters = [];

      //for (var i = 0; i < 1; i++) {
      for (var i = 0; i < this.state.message.length; i++) {
        //console.log("RANDOM DURATION: " + ((Math.random() * 8) + 1))
        //letters.push(<div className="help-text-individual" style={{animationDuration: `3s`}}>{this.state.message.charAt(i)}</div>);
        /*if(this.state.message.charAt(i) == " "){
          letters.push(<div className="help-text-individual" style={{visibility: "hidden"}}>_</div>);
        }else{*/
          //letters.push(<div className="help-text-individual" style={{animationDuration: `${this.state.offsets[i]}s`}}>{this.state.message.charAt(i)}</div>);
          letters.push(<div className="help-text-individual" style={{animationDuration: `${this.state.offsets[i]}s`}}>{this.state.message[i]}</div>);
        //}
      }

      return(
        <div className="helvetica" style={{textAlign: "center"}}>{letters}</div>
      );
    }

    render() {    
      return(<div className="help-text-container">
                {this.getIndividualLetters()}
              </div>);
    }
  }
  
  export default HelpText;